@import '../../styles/variables.scss';

.header-page-route {
    width: 100%;
    height: 100%;

    .header-page-route-container {
        width: 100%;
        padding: 80px 0;
        margin-bottom: 100px;
        background-color: $backgroudColor;

        .container {
            width: 80%;
            padding: 0 15px;
            margin: 0 auto;
            text-align: center;

            .header-page-route-title {
                font-size: 32px;
                font-weight: 500;
                margin-bottom: 10px;
            }

            & span a {
                color: $mainColor;
                text-decoration: none;
                margin-right: 4px;
            }
        }
    }

    .main-content {
        width: 80%;
        padding: 0 15px;
        margin: 0 auto;
        overflow: hidden;
        overflow-x: auto;
    }
}

@media only screen and (max-width: 740px) {
    .header-page-route {
        .main-content {
            width: 100%;
            margin: 0;
            padding: 0 15px;
            overflow: hidden;
        }
    }
}
